.coral3-Shell-workspaces {
  display: block;
}

.coral3-Shell-workspaces-workspace {
  display: inline-block;
  box-sizing: border-box;
  height: 42px;
  margin: 0 17.5px;
  border-bottom: 2px solid transparent;
  line-height: 30px;
  text-decoration: none;
  transition: border-color 0.21s ease-out, color 0.21s ease-out;
}

.coral3-Shell-workspaces-workspace:focus {
  outline: none;
}

.coral3-Shell-workspaces-workspace:first-of-type {
  margin-left: 0;
}

.coral3-Shell-workspaces-workspace {
  color: #999;
}

.coral3-Shell-workspaces-workspace:hover {
  color: #2680eb;
}

.coral3-Shell-workspaces-workspace.is-selected {
  border-color: #fff;
  color: #fff;
}

.coral3-Shell-workspaces-workspace:focus {
  border-color: #2680eb;
  color: #2680eb;
}