  float: right;
  margin-left: 15px;
}

.coral3-Shell-header-actions .coral3-Shell-menu-button {
  font-size: 12px;
}

.coral3-Shell-header-content {
  height: 42px;
  display: inline-block;
  vertical-align: top;
}

.coral3-Shell-homeAnchor {
  display: block;
  box-sizing: border-box;
  vertical-align: top;
  -webkit-transition: color 0.15s ease-out;
  transition: color 0.15s ease-out;
  margin: 0;
  padding: 0 8px;
  text-decoration: none;
  text-align: center;
  font-size: 14px;
  line-height: 32px;
  color: #999;
  cursor: pointer;
  white-space: nowrap;
}

